@import '../../../assets/css/theme';
@import '../../../assets/css/mixins';
.trans_notify {
  font-family: Poppins;
  display: flex;
  justify-content: flex-start;

  > img {
    width: 120px;
    height: 120px;
    @include mobile-only {
      width: 94px;
      height: 94px;
    }
  }

  .trans_notify_right {
    margin-left: 20px;
    @include mobile-only {
      margin-left: 10px;
    }
  }

  .typo_text_link,
  .trans_title {
    white-space: normal;
  }

  .trans_title {
    font-size: 20px;
    font-weight: 600;
    color: $color-text-default;
    line-height: 28px;
    @include mobile-only {
      font-size: 16px;
      line-height: 22px;
    }
  }

  .trans_massage {
    font-size: 15px;
    color: $color-text-default;
    line-height: 21px;
    margin-top: 8px;
    word-break: break-all;
    @include mobile-only {
      font-size: 14px;
      line-height: 20px;
    }
  }

  .trans_link {
    margin: 22px 0 0 0;
    img,
    svg {
      margin-left: 7px;
      @include mobile-only {
        position: relative;
        top: 2px;
      }
    }
    @include mobile-only {
      margin: 16px 0 0 0;
    }
  }
}
