@import '../../../assets/css/theme';
@import '../../../assets/css/mixins';

:global(#sunio) .common_btn {
  font-family: Poppins;
  height: 56px;
}
:global {
  .common_btn_primary {
    &:not(:disabled):hover {
      &:hover {
        background: #8c5bff !important;
      }
    }
    &.ant-btn-loading {
      background: rgba(100, 0, 255, 0.5) !important;
      &:not(:disabled):hover {
        background: rgba(100, 0, 255, 0.5) !important;
      }
    }
  }
}

:global(#sunio .common_btn_border) {
  border-width: 2px;
}

:global(#sunio .common_btn_gradient) {
  position: relative;
  height: 40px;
  line-height: 40px;
  padding: 0 33px;
  text-align: center;
  border-radius: 5px;
  outline: none;
  border: none;
  cursor: pointer;
  font-family: PingFangSC, PingFang SC, sans-serif;
  font-weight: 600;
  font-size: 16px;
  font-style: normal;
  background-image: linear-gradient(315deg, #8e59ff, #ba5bff);
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;

  &:hover {
    background: linear-gradient(315deg, #7700ff 0%, #8c51ff 100%);
    color: #fff;
  }
  &:hover svg {
    display: none;
  }
}

:global(#sunio .common_btn_gradient_bg) {
  line-height: 36px;
  text-align: center;
  border-radius: 5px;
  outline: none;
  border: none;
  cursor: pointer;
  font-family: PingFangSC, PingFang SC, sans-serif;
  font-weight: 600;
  font-size: 14px;
  font-style: normal;
  background-image: linear-gradient(315deg, #7700ff 0%, #8c51ff 100%);
  color: #fff;
  padding: 0 12px;
}

@include mobile-only {
  :global(#sunio) .common_btn {
    height: 40px;
  }
}
