@import './theme';
@import './mixins';

.base-footer {
  margin: 140px auto 0;
  border-top: 1px solid rgba(36, 33, 51, 1);
  padding: 80px 0 80px;
  width: 100%;
  max-width: min(1440px, 90vw);
  display: flex;
  justify-content: space-between;
  .footer-l {
    display: flex;
    flex-wrap: wrap;
    .footer-list {
      flex: 1;
      margin-right: 112px;
      text-align: left;
      &:last-child {
        margin-right: 0;
      }
      .footer-title {
        white-space: nowrap;
        margin-bottom: 15px;
        font-size: 14px;
        font-family: Poppins;
        font-weight: 600;
        color: #fff;
        line-height: 21px;
      }
      ul {
        li {
          margin-bottom: 15px;
          &:last-child {
            margin-bottom: 0;
          }
        }
        a {
          font-size: 14px;
          font-family: Poppins;
          font-weight: 400;
          line-height: 21px;
          white-space: nowrap;
          color: #fff;
          opacity: 0.5;
          transition: opacity 0.1s;
          &:hover {
            opacity: 1;
          }
        }
      }
    }
  }
  .footer-r {
    flex: 1;
    .sun-logo {
      // display: flex;
      // justify-content: flex-end;
      img {
        display: block;
        width: 151px;
      }
    }
  }

  .foot-hide-for-pc {
    display: block;
  }
  .foot-hide-for-mobile {
    display: none;
  }

  .footer-globalOutlined {
    font-size: 18px;
    color: #fff;
    margin-right: 6px;
  }
}

.copyright {
  margin-top: 30px;
  font-size: 14px;
  line-height: 21px;
  font-family: Poppins;
  color: #fff;
  opacity: 0.5;
  line-height: 14px;
}

@include pc-small-screen {
  .base-footer {
    max-width: 1200px;
    .footer-l {
      .footer-list {
        margin-right: 80px;
        &:last-child {
          margin-right: 0;
        }
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .base-footer {
    margin-top: 0;
    padding: 80px 25px 74px;
    max-width: 100%;
    border: 0;
    .footer-l .footer-list {
      margin-right: 0;
      flex: auto;
      &:nth-child(1) {
        width: calc(100% - 106px);
      }
      &:nth-child(2) {
        width: 106px;
      }
      &:nth-child(3) {
        width: calc(100% - 160px);
      }
      &:nth-child(4) {
        width: 160px;
      }
      &:nth-child(3),
      &:nth-child(4) {
        margin-top: 30px;
      }
      ul {
        li {
          margin-bottom: 12px;
          &:last-child {
            margin-bottom: 0;
          }
        }
      }
    }
    .footer-r {
      .sun-logo {
        display: flex;
        justify-content: flex-end;
        img {
          width: auto;
          height: 30px;
        }
      }
    }
    .foot-hide-for-pc {
      display: none;
    }
    .foot-hide-for-mobile {
      display: block;
    }
  }
  .copyright {
    position: absolute;
    bottom: 32px;
    right: 25px;
    font-size: 12px;
    line-height: 14px;
    margin-top: 0;
  }
}
