html {
  background: #0a0118;
  font-size: 10pt;

  body {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    min-height: 100vh;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    font-size: 14px;
    font-family: 'Poppins';

    .ant-modal {
      .ant-modal-close {
        top: 5px;
        right: 5px;
        width: 32px;
        height: 32px;
      }
    }
    .modal-btn {
      display: inline-block;
      font-weight: 500;
      color: #4e545a;
      font-family: inherit;
      font-size: inherit;
      background: transparent;
      padding: 0.25em 1.5em;
      position: relative;
      -webkit-transition: all 0.2s ease-in;
      transition: all 0.2s ease-in;
      border: 1px solid #4e545a;
      cursor: pointer;
      border-radius: 4px;
      box-sizing: border-box;
      line-height: 28px;
    }

    a {
      color: #5916e1;
      text-decoration: none;
      &:hover {
        color: #5916e1;
      }
    }

    // button:not(.pure-comp) {
    //   border: none;
    //   background: #5915e1;
    //   color: #ffffff;
    //   padding: 4px 10px;
    //   cursor: pointer;
    //   &:disabled {
    //     cursor: not-allowed;
    //     opacity: 0.3;
    //   }
    // }

    // antd tab style
    .ant-tabs-top > .ant-tabs-nav::before,
    .ant-tabs-bottom > .ant-tabs-nav::before,
    .ant-tabs-top > div > .ant-tabs-nav::before,
    .ant-tabs-bottom > div > .ant-tabs-nav::before {
      border: none;
    }

    .bold {
      font-weight: bold;
    }

    .ant-picker-focused {
      box-shadow: none;
      border-color: #e1e1e1;
    }

    .ant-picker:hover,
    .ant-picker-focused {
      box-shadow: none;
      border-color: #e1e1e1;
    }

    .ant-picker.active {
      border-color: #5916e1;
    }
  }

  // antd input style
  .ant-input-affix-wrapper-disabled {
    background: #fff;
  }

  // .ant-input-affix-wrapper-focused:not(.pure-comp) {
  //   border: 1px solid #5916e1;
  //   box-shadow: none;
  // }

  // #root .ant-input-affix-wrapper:not(.ant-input-affix-wrapper-disabled, .pure-comp):hover {
  //   border-color: #5916e1;
  // }

  // antd table style(pure-table 不被项目中的全局样式覆盖)
  .ant-table-wrapper:not(.pure-comp) {
    .ant-table {
      color: #333;
      background: transparent;
      border: none;

      .ant-table-thead > tr {
        & > th {
          color: #999;
          background: #fff;
          border: none;
          font-size: 14px;
          font-weight: normal;
          &:last-child {
            border-right: none;
          }
        }
        & > td:last-child {
          border-right: none;
        }
      }

      .ant-table-tbody {
        > tr:hover:not(.ant-table-expanded-row) > td,
        .ant-table-row-hover,
        .ant-table-row-hover > td {
          background: #fff !important;
        }

        tr td {
          border: none;
          padding: 5px;
          color: #333;
          text-align: left;
        }

        & > tr:last-child > td {
          border-bottom: none;
        }
      }

      table {
        border: none;
        display: flex;
        flex-wrap: wrap;

        thead,
        tbody {
          width: 100%;
        }

        thead {
          border-bottom: 1px solid #f2f2f2;
        }

        tr {
          display: flex;
          justify-content: space-between;
          width: 100%;
        }

        td,
        th {
          width: 25%;
          text-align: center;
        }
      }

      &.ant-table-empty {
        table tr {
          justify-content: center;
        }
      }
    }
    .ant-table-empty {
      .ant-table-placeholder {
        color: #fff;
      }
    }
  }

  // antd button style
  .ant-btn:not(.pure-comp),
  .ant-btn-primary:not(.pure-comp) {
    background: $color-button-primary;
    border: none;
    color: $white;

    &:not(:disabled):not(.ant-btn-disabled):hover {
      background-color: $color-button-primary;
      color: $white;
    }
  }
  .ant-btn.ant-btn-primary:disabled {
    background: $color-button-primary;
    color: #fff;
    cursor: not-allowed;
    opacity: 0.3;
  }

  .ib-parent div,
  .ib-parent p,
  .ib-parent h1,
  .ib-parent h2,
  .ib-parent h3,
  .ib-parent h4,
  .ib-parent h5,
  .ib-parent h6 {
    display: inline-block;
  }
}

